export default {

	data() {
		return {
			checkStatus: false,
			phoneNum: null,
			VerificationCode: null,
			VerificationMes: "获取验证码",
			disabled: false
		}
	},

	methods: {


		checkboxchange(e) {
			var values = e.detail.value;
			if (values.length > 0) {
				console.log("xz")
				this.checkStatus = true;
			} else {
				console.log("wxz")
				this.checkStatus = false;
			}

		},

		getVerificationCode() {


			if (!this.phoneNum||this.phoneNum.length!==11 ) {
				uni.showToast({
					title: '请输入正确的手机号！'
				});
			} else {
				var ii = 0;
				this.disabled = true;
				this.VerificationMes = "60s";
				this.timer = setInterval(() => {
				
					ii = ii + 1;
					console.log("ii=" + ii);
					this.VerificationMes = (60 - ii).toString() + "s";
				
					if (ii == 30) {
						this.disabled = false;
						this.VerificationMes = "获取验证码";
						ii = 0;
						clearInterval(this.timer);
					}
				}, 1000)
				
				this.$api.post('login!ajaxGetPhoneCode.action',{tel:this.phoneNum}).then(res=>{
					if (res.res.status == 0) {
						console.log("获取验证码成功")
					}
				})
				
			}

		},


		registclick() {
			if (this.checkStatus) {

				if (this.phoneNum.length == 11 && this.VerificationCode.length > 0) {
					// console.log("手机号"+this.phoneNum+"yzm"+this.VerificationCode)
					this.$api.post('login!ajaxReg.action', {
						tel: this.phoneNum,
						phoneCode: this.VerificationCode
					}).then(res => {
						if (res.res.status == 0) {
							
							//注册成功 调用登录接口
							this.$api.post('login!ajaxLogin.action', {
								tel: this.phoneNum,
								phoneCode: this.VerificationCode
							}).then(res => {
								if (res.res.status == 0) {
									var dict = res.inf;
								
									uni.setStorage({
										key:"user",
										data:dict,
										success() {
											uni.showToast({
												title: '注册成功！登录中···'
											});
											setTimeout(function() {
												uni.navigateBack({
													delta: 2
												})
											}, 1500)
										}
									})
									
									
								}

							})


						} else {
							uni.showToast({
								title: '注册失败！'
							});
						}
					})
				} else {
					uni.showToast({
						title: '请检查填写项！'
					});
				}

			} else {
				uni.showToast({
					title: '需要勾选阅读并同意《用户注册协议》'
				});
			}
		},


		jumpToxy() {
			uni.navigateTo({
				url: './UserRegistrationAgreement',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		}
	}
}
